import React, { Component } from "react";
import "./MainLeftItem.css";
import echarts from "echarts";

class MainLeftItem extends Component {
  constructor(props) {
    super(props);
    this.state = {
      active: true
    };
  }
  render() {
    return (
      <div className="MainLeftItem-wrapper">
        <div className="MainLeftItem-middle-wrapper">
          <div className="MainLeftItem-middle-wrapper-top">
            <div className="MainLeftItem-middle-wrapper-top-left">
              <div className="MainLeftItem-middle-wrapper-top-left-item">
                财富中收
              </div>
            </div>
            <div className="MainLeftItem-middle-wrapper-top-middle">
              <div className="MainLeftItem-middle-wrapper-top-middle-item">
                123,123,123
              </div>
            </div>
            <div className="MainLeftItem-middle-wrapper-top-right">
              <div className="MainLeftItem-middle-wrapper-top-right-item">
                T + 1
              </div>
            </div>
          </div>
          <div className="MainLeftItem-middle-wrapper-bottom">
            <div className="MainLeftItem-zhanwei"></div>
            <div className="MainLeftItem-middle-wrapper-bottom-left">
              <div className="MainLeftItem-middle-wrapper-bottom-left-left">
                当月累计
              </div>
              <div className="MainLeftItem-middle-wrapper-bottom-left-middle">
                123
              </div>
              <div className="MainLeftItem-middle-wrapper-bottom-left-right zhengzhangshang"></div>
            </div>
            <div className="MainLeftItem-middle-wrapper-bottom-right">
              <div className="MainLeftItem-middle-wrapper-bottom-left-left">
                当月累计
              </div>
              <div className="MainLeftItem-middle-wrapper-bottom-left-middle">
                -123,123
              </div>
              <div className="MainLeftItem-middle-wrapper-bottom-left-right zhengzhangxia "></div>
            </div>
          </div>
        </div>

        <div className="MainLeftItem-bottom-wrapper">
          <div className="MainLeftItem-bottom-wrapper-top">
            {this.state.active === true ? "近六月趋势" : "近五年趋势"}
          </div>
          <div className="MainLeftItem-bottom-wrapper-top-right">
            <div
              className={
                this.state.active === true
                  ? "MainLeftItem-bottom-wrapper-top-right-item-0ff"
                  : "MainLeftItem-bottom-wrapper-top-right-item-hui"
              }
              onClick={this.bindActiveClick.bind(this, true)}
            ></div>
            <div
              className={
                this.state.active === false
                  ? "MainLeftItem-bottom-wrapper-top-right-item-0ff"
                  : "MainLeftItem-bottom-wrapper-top-right-item-hui"
              }
              onClick={this.bindActiveClick.bind(this, false)}
            ></div>
          </div>
          <div
            className="MainLeftBottomItem-bottom-wrapper-slider"
            style={{ left: this.state.active === true ? "0%" : "-100%" }}
          >
            <div
              id="main-left-1"
              className="MainLeftItem-echarts-wrapper-1  MainLeftItem-echarts-wrapper-fir"
            ></div>
            <div
              id="main-left-2"
              className="MainLeftItem-echarts-wrapper-2 MainLeftItem-echarts-wrapper-sec"
            ></div>
          </div>
        </div>
      </div>
    );
  }
  bindActiveClick(tip) {
    if (tip === true) {
      if (this.state.active === true) {
        return;
      } else {
        this.setState({
          active: true
        });
      }
    } else {
      if (this.state.active === false) {
        return;
      } else {
        this.setState({
          active: false
        });
      }
    }
  }
  componentDidMount() {
    // 基于准备好的dom，初始化echarts实例
    let xDom = document.getElementById("main-left-1");
    let xDom2 = document.getElementById("main-left-2");
    let xDomId =
      "MainLeftBottomItem-echarts-wrapper-fir" + new Date().getTime();
    let xDom2Id =
      "MainLeftBottomItem-echarts-wrapper-sec" + new Date().getTime();
    xDom.setAttribute("id", xDomId);
    xDom2.setAttribute("id", xDom2Id);
    var myChart = echarts.init(document.getElementById(xDomId));
    var myChart2 = echarts.init(document.getElementById(xDom2Id));
    // 绘制图表
    var data = [190, 210, 150, 280, 200, 480, 190];
    var date = ["10/2", "10/3", "10/4", "10/5", "10/6", "10/7", "10/8"];
    var option = {
      tooltip: {
        trigger: "axis",
        position: function(pt) {
          return [pt[0], "10%"];
        }
      },
      grid: {
        left: 50,
        right: 20,
        top: 30,
        bottom: 30
      },
      xAxis: {
        type: "category",
        boundaryGap: false, //坐标轴两边留白策略
        axisLine: {
          lineStyle: {
            color: "#aaa",
            opacity: 0.5 //透明度
          }
        },
        axisLabel: {
          interval: 0,
          fontSize: 10
        },
        axisTick: {
          show: false
        },
        data: date
      },
      yAxis: {
        type: "value",
        boundaryGap: false,
        axisLine: {
          lineStyle: {
            color: "#aaa",
            opacity: 0.5
          }
        },
        axisTick: {
          show: false
        },
        splitLine: {
          lineStyle: {
            opacity: 0
          }
        },
        axisLabel: {
          interval: 0,
          fontSize: 10
        }
      },
      series: [
        {
          name: "营收",
          type: "line",
          smooth: true, //是平滑曲线显示
          symbol: "none", //标记的图形
          itemStyle: {
            color: "rgba(0,255,255,1)"
          },
          areaStyle: {
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
              {
                offset: 0,
                color: "rgba(0,255,255,0.5)"
              },
              {
                offset: 1,
                color: "rgba(0,255,255,0)"
              }
            ])
          },
          data: data
        }
      ]
    };
    myChart.setOption(option);
    myChart2.setOption(option);
  }
}

export default MainLeftItem;
