import React, { Component } from "react";
import "./MainLeftBottomItem.css";
import echarts from "echarts";

class MainLeftBottomItem extends Component {
  constructor(props) {
    super(props);
    this.state = {
      active: true
    };
  }
  render() {
    return (
      <div className="MainLeftBottomItem-wrapper">
        <div className="MainLeftBottomItem-top-title">贵金属</div>
        <div className="MainLeftBottomItem-middle-wrapper">
          <div className="MainLeftBottomItem-middle-wrapper-top">
            <div className="MainLeftBottomItem-middle-wrapper-top-top">
              当日实时销量
            </div>
            <div className="MainLeftBottomItem-middle-wrapper-top-bottom">
              123,123,123
            </div>
          </div>
          <div className="MainLeftBottomItem-middle-wrapper-bottom">
            <div className="MainLeftBottomItem-middle-wrapper-bottom-item border-right-1">
              <div className="MainLeftBottomItem-middle-wrapper-top-top">
                比上日
              </div>
              <div className="MainLeftBottomItem-middle-wrapper-bottom-item-bottom">
                <div className="MainLeftBottomItem-middle-wrapper-bottom-item-bottom-top">
                  123
                </div>
                <div className="MainLeftBottomItem-middle-wrapper-bottom-item-bottom-bottom-up"></div>
              </div>
            </div>
            <div className="MainLeftBottomItem-middle-wrapper-bottom-item">
              <div className="MainLeftBottomItem-middle-wrapper-top-top">
                比上月
              </div>
              <div className="MainLeftBottomItem-middle-wrapper-bottom-item-bottom">
                <div className="MainLeftBottomItem-middle-wrapper-bottom-item-bottom-top">
                  -12,123
                </div>
                <div className="MainLeftBottomItem-middle-wrapper-bottom-item-bottom-bottom-down"></div>
              </div>
            </div>
          </div>
        </div>

        <div className="MainLeftBottomItem-bottom-wrapper">
          <div className="MainLeftBottomItem-bottom-wrapper-top">
            {this.state.active === true ? "销量近三月趋势" : "销量近半年趋势"} 
          </div>
          <div className="MainLeftBottomItem-bottom-wrapper-top-right">
            <div
              className={
                this.state.active === true
                  ? "MainLeftBottomItem-bottom-wrapper-top-right-item-0ff"
                  : "MainLeftBottomItem-bottom-wrapper-top-right-item-hui"
              }
              onClick={this.bindActiveClick.bind(this, true)}
            ></div>
            <div
              className={
                this.state.active === false
                  ? "MainLeftBottomItem-bottom-wrapper-top-right-item-0ff"
                  : "MainLeftBottomItem-bottom-wrapper-top-right-item-hui"
              }
              onClick={this.bindActiveClick.bind(this, false)}
            ></div>
          </div>
          <div className="MainLeftBottomItem-bottom-wrapper-slider" style={{"left" : this.state.active === true ? "0%":"-100%"}}>
            <div
              id="main-bottom-1"
              className="MainLeftBottomItem-echarts-wrapper-1  MainLeftBottomItem-echarts-wrapper-fir"
            ></div>
            <div
              id="main-bottom-2"
              className="MainLeftBottomItem-echarts-wrapper-2 MainLeftBottomItem-echarts-wrapper-sec"
            ></div>
          </div>
        </div>
      </div>
    );
  }
  bindActiveClick(tip) {
    if (tip === true) {
      if (this.state.active === true) {
        return;
      } else {
        this.setState({
          active: true
        });
      }
    } else {
      if (this.state.active === false) {
        return;
      } else {
        this.setState({
          active: false
        });
      }
    }
  }

  componentDidMount() {
    // 基于准备好的dom，初始化echarts实例
    let xDom = document.getElementById('main-bottom-1')
    let xDom2 = document.getElementById('main-bottom-2')
    let xDomId = 'MainLeftBottomItem-echarts-wrapper-fir' + new Date().getTime();
    let xDom2Id = 'MainLeftBottomItem-echarts-wrapper-sec' + new Date().getTime();
    xDom.setAttribute('id', xDomId)
    xDom2.setAttribute('id', xDom2Id)
    var myChart = echarts.init(document.getElementById(xDomId));
    var myChart2 = echarts.init(document.getElementById(xDom2Id));
    // 绘制图表
    var data = [190, 210, 150, 280, 200, 480, 190];
    var date = ["10/2", "10/3", "10/4", "10/5", "10/6", "10/7", "10/8"];
    var option = {
      tooltip: {
        trigger: "axis",
        position: function(pt) {
          return [pt[0], "10%"];
        }
      },
      grid: {
        left: 40,
        right: 20,
        top: 30,
        bottom: 30
      },
      xAxis: {
        type: "category",
        boundaryGap: false, //坐标轴两边留白策略
        axisLine: {
          lineStyle: {
            color: "#aaa",
            opacity: 0.5 //透明度
          }
        },
        axisLabel: {
          interval: 0,
          fontSize: 10
        },
        axisTick: {
          show: false
        },
        data: date
      },
      yAxis: {
        type: "value",
        boundaryGap: false,
        axisLine: {
          lineStyle: {
            color: "#aaa",
            opacity: 0.5
          }
        },
        axisTick: {
          show: false
        },
        splitLine: {
          lineStyle: {
            opacity: 0
          }
        },
        axisLabel: {
          interval: 0,
          fontSize: 10
        }
      },
      series: [
        {
          name: "营收",
          type: "line",
          smooth: true, //是平滑曲线显示
          symbol: "none", //标记的图形
          itemStyle: {
            color: "rgba(0,255,255,1)"
          },
          areaStyle: {
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
              {
                offset: 0,
                color: "rgba(0,255,255,0.5)"
              },
              {
                offset: 1,
                color: "rgba(0,255,255,0)"
              }
            ])
          },
          data: data
        }
      ]
    };
    myChart.setOption(option);
    myChart2.setOption(option);
  }
}

export default MainLeftBottomItem;
