import React, { Component } from "react";
import "./MainRight.css";
import echarts from "echarts";

class MainRight extends Component {
  render() {
    return (
      <div className="mainright-wrapper">
        <div className="mainright-top">
          <div className="mainright-top-title">重点产品</div>
          <div className="mainright-top-item-1">
            <div className="mainright-top-item-1-img"></div>
            <div className="mainright-top-item-1-wrapper">
              <div className="huise-color-text">当月实时销量</div>
              <div className="big-weight-color-text">123,123,123</div>
            </div>
          </div>

          <div className="mainright-top-item-2">
            <div className="mainright-top-item-2-img"></div>
            <div className="mainright-top-item-1-wrapper">
              <div className="huise-color-text">当月实时销量</div>
              <div className="big-weight-color-text">123,123,123</div>
            </div>
          </div>
        </div>
        <div className="mainright-middle">
          <div className="mainright-middle-title">排名前5分行</div>
          <div
            id="main-right-1"
            style={{
              width: "100%",
              height: "100%"
            }}
          ></div>
        </div>
        <div className="mainright-bottom">
          <div className="mainright-middle-title">排名前5人员</div>
          <div
            id="main-right-2"
            style={{
              width: "100%",
              height: "100%"
            }}
          ></div>
        </div>
      </div>
    );
  }
  componentDidMount() {
    // 基于准备好的dom，初始化echarts实例
    var myChart = echarts.init(document.getElementById("main-right-1"));
    var myChart2 = echarts.init(document.getElementById("main-right-2"));
    // 绘制图表
    var data = [100, 90, 60, 40, 20];
    var titlename = ["1", "2", "3", "4", "5"];
    var valdata = ["上海", "杭州", "北京", "深圳", "广州"];
    var valdata1 = ["张三", "李四", "张三三", "张三四", "张三五"];
    var option = {
      grid: {
        left: 40,
        right: 50,
        top: 60,
        bottom: 30
      },
      xAxis: {
        show: false
      },
      yAxis: [
        {
          show: true,
          data: titlename,
          inverse: true,
          axisLine: {
            show: false
          },
          splitLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          axisLabel: {
            textStyle: {
              color: "#fff"
            },
            formatter: function(value, index) {
              return ["{warnValue" + index + "|" + value + "} "].join("\n");
            },
            rich: {
              //这里的rich，下面有解释
              warnValue0: {
                height: 20,
                width: 20,
                borderRadius: 50,
                borderColor: "rgb(255,217,71)",
                borderWidth: 2,
                lineHeight:20,
                align: "center",
                backgroundColor: "rgb(255,180,0)"
              },
              warnValue1: {
                height: 20,
                width: 20,
                borderRadius: 50,
                borderColor: "rgb(224,224,224)",
                borderWidth: 2,
                lineHeight:20,
                align: "center",
                backgroundColor: "rgb(170,170,170)"
              },
              warnValue2: {
                height: 20,
                width: 20,
                borderRadius: 50,
                borderColor: "rgb(240,219,145)",
                borderWidth: 2,
                lineHeight:20,
                align: "center",
                backgroundColor: "rgb(215,169,92)"
              },
              warnValue3: {
                height: 20,
                width: 20,
                borderRadius: 50,
                borderWidth: 2,
                lineHeight:20,
                align: "center",
              },
              warnValue4: {
                height: 20,
                width: 20,
                borderRadius: 50,
                borderWidth: 2,
                lineHeight:20,
                align: "center",
              }
            }
          }
        },
        {
          show: true,
          inverse: true,
          data: valdata,
          axisLabel: {
            textStyle: {
              color: "#fff"
            }
          },
          axisLine: {
            show: false
          },
          splitLine: {
            show: false
          },
          axisTick: {
            show: false
          }
        }
      ],
      series: [
        {
          name: "条",
          type: "bar",
          yAxisIndex: 0,
          data: data,
          barWidth: 10,
          itemStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [
                {
                  color: "rgba(0,255,255,0)",
                  offset: 0
                },
                {
                  color: "rgba(0,255,255,1)",
                  offset: 1
                }
              ])
            }
          },
          label: {
            normal: {
              show: false,
              position: "inside",
              formatter: "{c}%"
            }
          }
        }
      ]
    };
    var option1 = {
        grid: {
          left: 40,
          right: 50,
          top: 60,
          bottom: 30
        },
        xAxis: {
          show: false
        },
        yAxis: [
          {
            show: true,
            data: titlename,
            inverse: true,
            axisLine: {
              show: false
            },
            splitLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              textStyle: {
                color: "#fff"
              },
              formatter: function(value, index) {
                return ["{warnValue" + index + "|" + value + "} "].join("\n");
              },
              rich: {
                //这里的rich，下面有解释
                warnValue0: {
                  height: 20,
                  width: 20,
                  borderRadius: 50,
                  borderColor: "rgb(255,217,71)",
                  borderWidth: 2,
                  lineHeight:20,
                  align: "center",
                  backgroundColor: "rgb(255,180,0)"
                },
                warnValue1: {
                  height: 20,
                  width: 20,
                  borderRadius: 50,
                  borderColor: "rgb(224,224,224)",
                  borderWidth: 2,
                  lineHeight:20,
                  align: "center",
                  backgroundColor: "rgb(170,170,170)"
                },
                warnValue2: {
                  height: 20,
                  width: 20,
                  borderRadius: 50,
                  borderColor: "rgb(240,219,145)",
                  borderWidth: 2,
                  lineHeight:20,
                  align: "center",
                  backgroundColor: "rgb(215,169,92)"
                },
                warnValue3: {
                  height: 20,
                  width: 20,
                  borderRadius: 50,
                  borderWidth: 2,
                  lineHeight:20,
                  align: "center",
                },
                warnValue4: {
                  height: 20,
                  width: 20,
                  borderRadius: 50,
                  borderWidth: 2,
                  lineHeight:20,
                  align: "center",
                }
              }
            }
          },
          {
            show: true,
            inverse: true,
            data: valdata1,
            axisLabel: {
              textStyle: {
                color: "#fff"
              }
            },
            axisLine: {
              show: false
            },
            splitLine: {
              show: false
            },
            axisTick: {
              show: false
            }
          }
        ],
        series: [
          {
            name: "条",
            type: "bar",
            yAxisIndex: 0,
            data: data,
            barWidth: 10,
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [
                  {
                    color: "rgba(0,255,255,0)",
                    offset: 0
                  },
                  {
                    color: "rgba(0,255,255,1)",
                    offset: 1
                  }
                ])
              }
            },
            label: {
              normal: {
                show: false,
                position: "inside",
                formatter: "{c}%"
              }
            }
          }
        ]
      };
    myChart.setOption(option);
    myChart2.setOption(option1);
  }
}

export default MainRight;
